<template>
  <div id="about" class="animate__animated animate__fadeIn">

    <div class="content">
      <div>
        <el-avatar :src="user.avatar" :size="100" style="display: inline-block;"></el-avatar>
      </div>
      <div>
        <div style="margin-top: 10px;"></div>
        <ul class="info">
          <li><span><a-icon type="user" /> {{ user.name }}</span></li>
          <li><span><a-icon type="man" /> {{ user.sex }}</span></li>
          <li><span><a-icon type="environment" /> {{ user.address }} &nbsp;&nbsp;&nbsp;&nbsp;<a-icon type="home" /> </span></li>
          <li><span><a-icon type="bug" /> {{ user.job }}</span></li>
          <li><span><a-icon type="message" /> {{ user.msg }}</span></li>
        </ul>
      </div>
    </div>

  </div>
  
</template>

<script>
export default {
  data() {
    return {
      user: {
        name: '',
        avatar: 'http://mrqinzh.info:9090/img/avatar.jpg',
        sex: '',
        address: '',
        job: '00Java',
        msg: 'yyds!yes',
      },
    }
  },
  mounted() {

  },
  
}
</script>

<style lang="scss">
  #about .content {
    width: 60%;
    height: 100%;
    margin: 30px auto;
    padding: 20px;
    background-color: white;
    font-size: 18px;
    .file {
      float: right;
    }
    .info {
      margin-left: 30px;
      line-height: 2em;
      font-family: ;
    }
  }

</style>


