<template>
  <!--  -->
  <div class="animate__animated animate__fadeInUp notice">
    <i class="layui-icon layui-icon-speaker" style="margin-left: 10px;font-size: 30px;color: #c26565"></i>
    <div class="roll">
      <ul>
        <li v-for="(item, index) in ulList" 
        :key="item.id" 
        :class="!index && play ? 'toUp' : ''">
          <span>{{item.msg}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {

  data() {
    return {
      play: false,
      ulList: [
        { msg: '' },
        { msg: '' },
        { msg: '' },
      ],
    }
  },

  methods: {
    // 
    startPlay() {
      let that = this
      that.play = true //
      setTimeout(() => {
        that.ulList.push(that.ulList[0]) //
        that.ulList.shift() //
        that.play = false //
      }, 500)
    },
  }, 
  mounted() {
    setInterval(this.startPlay, 4000);
  }
}
</script>

<style>
  .notice {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    background-image: linear-gradient(-225deg, #FFFEFF 0%, #D7FFFE 100%);
  }
  /*  */
  .roll {
    display: inline-block;
    height: 21px;
    position: relative;
    overflow: hidden;
    margin-left: 10px;
  }
  .roll ul {
    list-style: none;
    overflow: hidden;
    height: 20px;
    padding: 0;
    margin: 0;
  }
  .roll li {
    text-align: left;
    height: 20px;
    line-height: 20px;
  }
  .toUp {
    margin-top: -20px;
    transition: all 1s;
  }

</style>


