<template>
  <div>
    <el-input
    v-model="val"
    maxlength="12"
    prefix-icon="el-icon-search"
    placeholder="">
      <el-button @click="Search()" slot="append" icon="el-icon-search"></el-button>
    </el-input>
  </div>
</template>

<script>
export default {
  data() {
    return {
      val: '',
    }
  },
  methods: {
    Search() {
      if(this.val === '') {
        this.$message.warning(' zzz');
        return;
      }
      this.val = '';
      this.$message.success(' => ^_^');
    }
  },
}
</script>

<style>

</style>


